import { Component, OnInit } from '@angular/core';
import { CompanyDepartemenService, CompanyJabatanService, CompanyKantorService, CompanyDivisiService, DirektoriPegawaiPosisiService, DirektoriPegawaiMasterService, CompanyPangkatService, DirektoriPegawaiStatusService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { Pegawai_posisi, Pegawai, Jabatan, Kantor, Departemen, Divisi, Pangkat, Pegawai_status } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private companyDepartemenService: CompanyDepartemenService,
        private companyJabatanService: CompanyJabatanService,
        private companyKantorService: CompanyKantorService,
        private companyDivisiService: CompanyDivisiService,
        private companyPangkatService: CompanyPangkatService,
        private direktoriPegawaiStatusService: DirektoriPegawaiStatusService,
        private direktoriPegawaiPosisiService: DirektoriPegawaiPosisiService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pesan: PesanService,
        private pegawaiDetailService: PegawaiDetailService,
        private fb: FormBuilderTypeSafe,
    ) { }

    id_pegawai: string = ''
    pegawai_posisi: Pegawai_posisi = {}
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPreeData()
        this.getDepartemen()
        this.form()
    }

    listPegawai: Pegawai[] = []
    listJabatan: Jabatan[] = []
    listKantor: Kantor[] = []
    listDepartemen: Departemen[] = []
    listDivisi: Divisi[] = []
    listPangkat: Pangkat[] = []
    listStatus: Pegawai_status[] = []

    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })
        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })
        this.companyPangkatService.getCompanyPangkats().subscribe(data => {
            this.listPangkat = data.data
        })
        this.direktoriPegawaiStatusService.getDirektoriPegawaiStatuses().subscribe(data => {
            this.listStatus = data.data
        })
    }

    getDepartemen() {

        this.companyDepartemenService.getCompanyDepartemens().subscribe(data => {
            this.listDepartemen = data.data
        })
    }

    getDivisi() {
        this.spinner.show()
        this.companyDivisiService.getCompanyDivisis(JSON.stringify(
            {
                id_departemen: this.dataForm.value.id_departement,
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listDivisi = data.data;
                }
            )
    }

    getJabatan() {
        this.spinner.show()
        this.companyJabatanService.getCompanyJabatans(JSON.stringify(
            {
                id_divisi: this.dataForm.value.id_divisi,
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listJabatan = data.data;
                    if(data.data.length < 1)
                    {
                        this.pesan.pesanWarningForm('data jabatan kosong!')
                    }
                }
            )

    }

    dataForm: FormGroupTypeSafe<Pegawai_posisi>
    form() {
        this.dataForm = this.fb.group<Pegawai_posisi>({
            id_departement: new FormControl(''),
            id_divisi: new FormControl(''),
            id_jabatan: new FormControl('', [Validators.required]),
            id_kantor: new FormControl(''),
            id_pegawai_atasan: new FormControl(''),
            tanggal_diangkat: new FormControl(''),
            tanggal_dicabut: new FormControl(''),
            id_pangkat: new FormControl(''),
            id_status: new FormControl(''),
            nomor_sk: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),

        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form tidak valid')
            this.spinner.hide()
            return
        }
        this.pegawai_posisi = this.dataForm.value
        this.direktoriPegawaiPosisiService
            .postDirektoriPegawaiPosisis(this.pegawai_posisi)
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'hubungan-kerja', 'list'])
    }

}
