import { DirektoriPegawaiPosisiService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { Pegawai_posisi } from 'src/app/sdk/hr/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPegawaiPosisiService: DirektoriPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    dataPosisi: Pegawai_posisi[] = []
    id_pegawai_posisi: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPosisi()
    }


    getPosisi() {
        this.direktoriPegawaiPosisiService.getDirektoriPegawaiPosisis(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataPosisi = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPegawaiPosisiService
            .deleteDirektoriPegawaiPosisisId(id)
            .subscribe(data => {
                this.getPosisi()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'biodata', 'detail'])
    }

}
