import { Component, OnInit } from '@angular/core';
import { CompanyDepartemenService, CompanyJabatanService, CompanyKantorService, CompanyDivisiService, DirektoriPegawaiPosisiService, DirektoriPegawaiMasterService, CompanyPangkatService, DirektoriPegawaiStatusService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { Pegawai_posisi, Pegawai, Jabatan, Kantor, Departemen, Divisi, Pangkat, Pegawai_status } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private companyDepartemenService: CompanyDepartemenService,
        private companyJabatanService: CompanyJabatanService,
        private companyKantorService: CompanyKantorService,
        private companyDivisiService: CompanyDivisiService,
        private companyPangkatService: CompanyPangkatService,
        private direktoriPegawaiStatusService: DirektoriPegawaiStatusService,
        private direktoriPegawaiPosisiService: DirektoriPegawaiPosisiService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private pegawaiDetailService: PegawaiDetailService,
        private fb: FormBuilderTypeSafe,
        private singleUploadService: SingleUploadService,
    ) { }

    id_pegawai: string = ''
    pegawai_posisi: Pegawai_posisi = {}

    id_pegawai_posisi: string = ''
    toogleUpload = false
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.activatedRoute.paramMap.subscribe(data => {
            if (data.get('id_pegawai_posisi')) {
                this.id_pegawai_posisi = data.get('id_pegawai_posisi')
                console.log(data.get('id_pegawai_posisi'))

                this.getPreeData()
                this.getData()
                this.form()
            }
        })

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'pegawai_posisi.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )

    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    listPegawai: Pegawai[] = []
    listJabatan: Jabatan[] = []
    listKantor: Kantor[] = []
    listDepartemen: Departemen[] = []
    listDivisi: Divisi[] = []
    listPangkat: Pangkat[] = []
    listStatus: Pegawai_status[] = []

    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })
        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })
        this.companyPangkatService.getCompanyPangkats().subscribe(data => {
            this.listPangkat = data.data
        })
        this.direktoriPegawaiStatusService.getDirektoriPegawaiStatuses().subscribe(data => {
            this.listStatus = data.data
        })
        this.companyDepartemenService.getCompanyDepartemens().subscribe(data => {
            this.listDepartemen = data.data
        })

    }

    getDivisi() {
        this.spinner.show()
        this.companyDivisiService.getCompanyDivisis(JSON.stringify(
            {
                id_departemen: this.dataForm.value.id_departement,
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listDivisi = data.data;
                }
            )
    }

    getJabatan() {
        this.spinner.show()
        this.companyJabatanService.getCompanyJabatans(JSON.stringify(
            {
                id_divisi: this.dataForm.value.id_divisi,
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listJabatan = data.data;
                    if(data.data.length < 1)
                    {
                        this.pesan.pesanWarningForm('data jabatan kosong!')
                    }
                }
            )

    }

    getData() {
        this.spinner.show()
        this.direktoriPegawaiPosisiService.getDirektoriPegawaiPosisisId(this.id_pegawai_posisi).subscribe(data => {
            this.dataForm.setValue(data.data)
            this.spinner.hide()

            this.singleUploadService.param.next({
                for_module: "pegawai_posisi",
                for_table: "erp_hr.pegawai_posisi",
                for_module_id: data.data.id_pegawai_posisi
            })
        })
    }

    dataForm: FormGroupTypeSafe<Pegawai_posisi>
    form() {
        this.dataForm = this.fb.group<Pegawai_posisi>({
            id_pegawai: new FormControl('', [Validators.required]),
            id_departement: new FormControl(''),
            id_divisi: new FormControl(''),
            id_jabatan: new FormControl(''),
            id_kantor: new FormControl(''),
            id_pangkat: new FormControl(''),
            id_pegawai_atasan: new FormControl(''),
            id_pegawai_posisi: new FormControl(''),
            id_perusahaan: new FormControl(''),
            id_status: new FormControl(''),
            id_dokumen: new FormControl(''),
            nomor_dokumen: new FormControl(''),
            tipe_dokumen: new FormControl(''),
            id_user_create: new FormControl(''),
            tanggal_diangkat: new FormControl(''),
            tanggal_dicabut: new FormControl(''),
            pegawai_status: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            nomor_sk: new FormControl(''),
            id_data_flow: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            nomor: new FormControl(''),
            posisi: new FormControl(''),
            status: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form tidak valid')
            this.spinner.hide()
            return
        }
        this.pegawai_posisi = this.dataForm.value
        this.direktoriPegawaiPosisiService
            .putDirektoriPegawaiPosisisId({ id: this.id_pegawai_posisi, body: this.pegawai_posisi })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'hubungan-kerja', 'list'])
    }

}
