import { Departemen } from 'src/app/sdk/hr/models/departemen';
import { Jabatan, Divisi, Pangkat, Kantor, Pegawai_status, Pegawai_master } from 'src/app/sdk/hr/models';
import { Pegawai_posisi } from 'src/app/sdk/hr/models/pegawai-_posisi';
import { Pegawai } from 'src/app/sdk/hr/models/pegawai';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiService, DirektoriPegawaiPosisiService, CompanyJabatanService, CompanyKantorService, CompanyPangkatService, CompanyDepartemenService, CompanyDivisiService, DirektoriPegawaiMasterService, DirektoriPegawaiStatusService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private direktoriPegawaiService: DirektoriPegawaiService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private direktoriPegawaiStatusService: DirektoriPegawaiStatusService,
        private companyJabatanService: CompanyJabatanService,
        private companyKantorService: CompanyKantorService,
        private companyPangkatService: CompanyPangkatService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyDivisiService: CompanyDivisiService,
        private direktoriPegawaiPosisiService: DirektoriPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService,
    ) { }

    id_pegawai: string = ''

    pegawai: Pegawai = {}
    PegawaiMaster: Pegawai_master = {}
    jabatan: Jabatan = {}
    departement: Departemen = {}
    divisi: Divisi = {}
    pangkat: Pangkat = {}
    kantor: Kantor = {}
    dataStatus: Pegawai_status = {}

    pegawai_posisi: Pegawai_posisi = {}

    id_pegawai_posisi: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.activatedRoute.paramMap.subscribe(data => {
            if (data.get('id_pegawai_posisi')) {
                this.id_pegawai_posisi = data.get('id_pegawai_posisi')
                console.log(data.get('id_pegawai_posisi'))

            }

            this.getPosisi()
            this.getLog()
        })

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getPosisi()
                    this.getLog()
                }
            }
        )
    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getPosisi() {
        this.spinner.show()
        this.direktoriPegawaiPosisiService.getDirektoriPegawaiPosisisId(this.id_pegawai_posisi)
            .subscribe(data => {
                this.pegawai_posisi = data.data
                this.getPegawai(data.data.id_pegawai)
                this.getJabatan(data.data.id_jabatan)
                this.getDepartemen(data.data.id_departement)
                this.getDivisi(data.data.id_divisi)
                this.getPangkat(data.data.id_pangkat)
                this.getKantor(data.data.id_kantor)
                this.getPegawaiMaster(data.data.id_pegawai_atasan)
                this.getStatus(data.data.id_status)

                this.params = {
                    id: data.data.id_pegawai_posisi,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_pegawai_posisi"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            })
    }

    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai).subscribe(
            data => {
                this.pegawai = data.data;
            }
        )
    }
    getPegawaiMaster(id_pegawai_atasan: string) {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMastersId(id_pegawai_atasan).subscribe(
            data => {
                this.PegawaiMaster = data.data;
            }
        )
    }
    getJabatan(id_jabatan: string) {
        this.companyJabatanService.getCompanyJabatansId(id_jabatan).subscribe(
            data => {
                this.jabatan = data.data;
            }
        )
    }
    getDepartemen(id_departement) {
        this.companyDepartemenService.getCompanyDepartemensId(id_departement).subscribe(
            data => {
                this.departement = data.data;
            }
        )
    }
    getDivisi(id_divisi) {
        this.companyDivisiService.getCompanyDivisisId(id_divisi).subscribe(
            data => {
                this.divisi = data.data;
            }
        )
    }
    getPangkat(id_pangkat) {
        this.companyPangkatService.getCompanyPangkatsId(id_pangkat).subscribe(
            data => {
                this.pangkat = data.data;
            }
        )
    }
    getKantor(id_kantor) {
        this.companyKantorService.getCompanyKantorsId(id_kantor).subscribe(
            data => {
                this.kantor = data.data;
                this.spinner.hide()
            }
        )
    }
    getStatus(id_status) {
        this.direktoriPegawaiStatusService.getDirektoriPegawaiStatusesId(id_status).subscribe(
            data => {
                this.dataStatus = data.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_pegawai_posisi })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'hubungan-kerja', 'list'])
    }

}


