import { DirektoriPegawaiPosisiService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { Pegawai_posisi } from 'src/app/sdk/hr/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPegawaiPosisiService: DirektoriPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService,
        private direktoriPegawaiService: DirektoriPegawaiService
    ) { }

    id_pegawai: string = ''
    dataPosisi = []
    id_pegawai_posisi: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPosisi()
    }


    getPosisi() {
        this.direktoriPegawaiPosisiService.getDirektoriPegawaiPosisis(JSON.stringify({ id_pegawai: this.id_pegawai, sortKey: this.sortKey, sortValue: this.sortValue })).subscribe(
            data => {
                this.dataPosisi = data.data
                this.dataPosisi.forEach((item, index) => {
                    this.getPegawai(index, item.id_pegawai_atasan);
                })
            }
        )
    }

    sortKey: string | null = null;
    sortValue: string | null = null;
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.getPosisi();
    }

    getPegawai(index, id_pegawai) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            data => {
                this.dataPosisi[index]['pegawai'] = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPegawaiPosisiService
            .deleteDirektoriPegawaiPosisisId(id)
            .subscribe(data => {
                this.getPosisi()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'biodata', 'detail'])
    }

}
